@import './variables';

:root {
  --brand-color: #40bee2;
  --black-bg-color: #{$brand-bg-color};
  --gray-bg-color: #{$extra-bg-color};
  --danger-color: #{$color-danger};
  --text-color: #{$text-color};
  --text-on-brand-color: #fff;

  --header-bg-color: #{$header-bg-color};

  --logo-bg-color: var(--header-bg-color);

  --sidebar-bg-color: var(--header-bg-color);
  --sidebar-color: #{$sidebar-color};
  --sidebar-item-active-bg-color: var(--brand-color);
  --sidebar-group-bg-color: var(--gray-bg-color);

  --btn-border-radius: #{$btn-border-radius};
  --btn-success-color: var(--brand-color);
  --btn-warning-color: var(--brand-color);

  --link-hover-color: var(--brand-color);

  --rc-reserved-color: var(--gray-bg-color);
  --rc-reserved-own-color: var(--brand-color);
  --rc-reserved-own-text-color: var(--text-on-brand-color);

  --panel-border-radius: #{$panel-border-radius};
  --panel-shadow-radius: #{$panel-shadow-radius};
  --panel-shadow-opacity: #{$panel-shadow-opacity};

  --font-name-major: #{$font-name-major};
  --font-name-minor: #{$font-name-minor};
  --font-family-major: var(--font-name-major), serif;
  --font-family-minor: var(--font-name-minor), sans-serif;
}
