@import '~scss/variables';
@import '~scss/custom-properties';

.message-board {
  ff-panel {
    max-width: 40rem;
  }

  .msg-sticky {
    margin-top: -.5rem;
    position: sticky;
    top: 0;
    z-index: 5;
    background-color: $ff-panel-bg-color;

    ff-textarea {
      min-height: 0;

      // characters counter, hides empty values /1000 and 0/1000
      &:not([counter^='/']):not([counter^='0/'])::after {
        content: attr(counter);
        position: absolute;
        right: 5px;
        top: 100%;
        font-size: .85em;
        color: var(--brand-color);
      }

    }
    .ff-textarea {
      line-height: 1.3;
      height: 70px;
      resize: vertical;
    }
  }

  .msg-messages-header {
    margin-top: .5rem;
  }

  .msg-post-header {
    padding-top: .5rem;
  }

  .msg-buttons {
    flex: 0 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    z-index: 2;

    .ff-static {
      padding: 0;
      flex-flow: column;
      justify-content: center;
    }

    ff-btn {
      margin: 2px 0;

      .ff-btn {
        min-width: 30px;
        padding: 4px;
        justify-content: center;
        font-size: 18px;
      }
    }
  }

  .msg-scroller > .msg-container.msg-container:first-of-type {
    margin-top: .5rem;
  }

  .msg-container.active {
    &::before {
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      z-index: 1;
      outline: 2px solid var(--brand-color);
      outline-offset: -2px;
      background: rgba(255,255,255,.4);
    }
  }

  .msg-body {
    line-height: 1.3;
  }
}
