/* Media Queries */
$screen-xs: 414px; // iPhone 8 Plus
$screen-sm: 480px;
$screen-md: 640px;
$screen-lg: 768px;
$screen-xl: 1024px;

$font-name-major: Optima;
$font-name-minor: Lato;
$font-size-base: 1.6rem !default;

$main-max-width-narrow: 960px;
$main-max-width: none;
//$main-max-width: 1200px;
$collapse-sidebar-width: 992px;

//$brand-color: #40bee2;
$brand-bg-color: #000;
$extra-bg-color: #949594;
$color-danger: #b00;

$text-color: #4d4e4c;
$text-on-brand-color: #fff;

$link-color: $text-color;

$transition-duration: .1s;

/* Header */
$header-bg-color: $brand-bg-color;
//$header-color: #fff;
$header-banner-bar-size: 60px;

/* Sidebar */
$sidebar-width: 220px;
$sidebar-breakpoint: 992px;

$sidebar-color: #ddd;

$sidebar-group-bg-color: $extra-bg-color;
$sidebar-group-color: #fff;
$sidebar-group-border-color: #dbdbda;
$sidebar-group-hover-bg-color: darken($sidebar-group-bg-color, 10%);
$sidebar-group-hover-color: #fff;
$sidebar-group-hover-border-color: $sidebar-group-border-color;
$sidebar-group-active-bg-color: $brand-bg-color;
$sidebar-group-active-color: #fff;

$sidebar-item-bg-color: #fff;
$sidebar-item-color: $text-color;
$sidebar-item-border-color: $sidebar-group-border-color;
$sidebar-item-hover-bg-color: #f5f5f5;
$sidebar-item-hover-color: #000;
$sidebar-item-hover-border-color: #aaa;
$sidebar-item-active-border-color: $sidebar-item-border-color;

/* Buttons */
$button-border-width: 2px;
$btn-border-radius: 12px;
$btn-primary-color: fade_out($text-color, .3);
$btn-danger-color: $color-danger;

/* Flex forms */
$ff-board-bg-color: #e4e4e3;
$ff-panel-bg-color: #fff;
$panel-border-radius: 15px;
$panel-shadow-radius: 30px;
$panel-shadow-opacity: 20%;
$ff-control-border-color: #b5b6b5;
$ff-control-min-height: 2.4rem;
$ff-control-padding: .3333rem;

/* Reservation Calendar */
$rc-sidebar-width: 180px;    // width of calendar sidebar (rental unit names)
$rc-sidebar-width-xs: 120px; // width of calendar sidebar for extra small screens

$rc-unavailable-color: #f2f2f2;        // unavailable space color in calendar
$rc-available-color: #fff;             // available space color in calendar

$rc-border-light-color: rgba(190, 190, 190, 0.2);
$rc-border-dark-color: #b2b2b2;

$rc-row-hover-color: #f9f9f9;
$rc-row-group-header-bg-color: $brand-bg-color;
$rc-row-group-header-color: #fff;

$rc-row-group-height: 20px; // height of reservation unit group header
$rc-row-height: 36px;       // height of calendar row

$rc-toolbar-height-day: 62px;  // height of toolbar in day mode
$rc-toolbar-height-week: 58px; // height of toolbar in week mode

$rc-header-height-day: 30px;  // height of time header in day mode
$rc-header-height-week: 60px; // height of time header in week mode

$rc-mq-sticky: "(min-height: 320px) and (min-width: 580px)"; // media query to make calendar with sticky header

/* Reservation time selector */
$time-selector-row-height: 37px;
$time-selector-time-col-width: 68px;

/* Message board */
$message-board-active-outline-color: #d3d3d3;
